main {
  text-align: center;
  color: #303030;
  position: relative;
}

/* Field params */
.game__board {
  height: 500px;
  background-color: #fff;
  border: 1px solid #000;
  position: relative;
  overflow-y: hidden;
  margin: 1em 2em;
}

/* Dot positioning */
.dot {
  border: 1px solid #000;
  opacity: 0.8;
  border-radius: 50%;
  position: absolute;
}

.score {
  font-size: 2rem;
  font-weight: 700;
}

label {
  margin: 0.5em;
}

.btn {
  background-color: #f8f8f8;
  border: 1px solid #bbbbbb;
  border-radius: 3px;
  padding: 6px 18px;
  margin: 12px;
  color: #303030;
  letter-spacing: 0.1em;
}

@keyframes slideDown {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(105vh);
  }
}
